import * as React from "react";
import HomeContainer from "../components/Container";
import IconButton from "@mui/material/IconButton";
import PlayCircleIcon from "@mui/icons-material/PlayCircle";
import Typography from "@mui/material/Typography";
import ChristmasList from "../components/ChristmasList";
import Grid from "@mui/material/Grid";

const Home = () => {
  let audio = new Audio("/christmasSong.mp3");
  const start = () => {
    audio.play();
  };
  return (
    <div>
      <HomeContainer>
        <div style={{ height: "50px" }} />
        <Grid container spacing={2}>
          <Grid item xs={12} md={12} style={{ backgroundColor: "#346633" }}>
            <Typography variant="h4" component="div" style={{ color: "white" }}>
              Welcome to my christmas wish-list website :) Click for a suprise:
            </Typography>
            <IconButton aria-label="delete" size="small" onClick={start}>
              <img
                src="/phoebeSanta.png"
                alt="Logo"
                style={{ height: "250px" }}
              />
            </IconButton>
          </Grid>
          <Grid item xs={12} md={6}></Grid>
        </Grid>
        <div style={{ height: "50px" }} />
        <ChristmasList />
      </HomeContainer>
    </div>
  );
};
export default Home;
